import React from "react"
import Img from "gatsby-image"
import { graphql } from "gatsby"
import Helmet from "react-helmet"
import Layout from "../components/layout"

export default ({ data }) => (
    <Layout>
        <Helmet
            bodyAttributes={{
                class: "page n8",
            }}
            htmlAttributes = {
                {
                    lang: "en"
                }
            }
        />

        <div className="banner">
            <h1>N8 Research</h1>
            <Img fluid={data.microscopes.childImageSharp.fluid} />
        </div>

        <section>
            <p>
                The N8 Research Partnership is a collaboration of the eight most research intensive Universities in the North of England: Durham, Lancaster, Leeds, Liverpool, Manchester, Newcastle, Sheffield, and York.
            </p>

            <div className="page__image-wrapper">
                <Img fluid={data.n8Old.childImageSharp.fluid} />
                <Img fluid={data.n8New.childImageSharp.fluid} />
            </div>

            <div className="text-center">
                <p>
                    <a href="https://www.n8research.org.uk" target="_blank" rel="noopener noreferrer">n8research.org.uk</a>
                </p>

                <p>
                    <small>
                        The old site is still available via <a href="https://web.archive.org/web/20150331102125/http://www.n8research.org.uk:80/" target="_blank" rel="noopener noreferrer">Wayback Machine</a>
                    </small>
                </p>
            </div>

            <p>
                We were approached to refresh the website, which is intended to be a central hub for all information relating to the research these Universities have and are currently undertaking. Due to the amount of information that could be added to the site over time the design was kept simple, with an easy layout to help users easily navigate and use the site.
            </p>

            <p>
                Built using WordPress, using multiple custom post types, content can be updated in the back end quickly and easily. With the use of Advanced Custom Fields as well we were able to tailor the site to the client's needs.
            </p>
        </section>
    </Layout>
)

export const pageQuery = graphql`
    query {
        microscopes: file(relativePath: { eq: "microscopes.jpg" }) {
            childImageSharp {
                fluid(quality: 100, maxWidth: 2560) {
                    ...GatsbyImageSharpFluid_withWebp
                }
            }
        }
        n8Old: file(relativePath: { eq: "n8-old.jpg" }) {
            childImageSharp {
                fluid(quality: 100, maxWidth: 2560) {
                    ...GatsbyImageSharpFluid_withWebp
                }
            }
        }
        n8New: file(relativePath: { eq: "n8-new.jpg" }) {
            childImageSharp {
                fluid(quality: 100, maxWidth: 2560) {
                    ...GatsbyImageSharpFluid_withWebp
                }
            }
        }
    }
`;